import React, { useState } from "react";
import styled from "styled-components";
import { motion, AnimatePresence } from "framer-motion";

const Wrapper = styled.div`
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column; /* 수직 중앙 정렬을 위해 컬럼 방향으로 설정 */
  align-items: center; /* 수평 중앙 정렬 */
  justify-content: center; /* 수직 중앙 정렬 */
`;

const Grid = styled.div`
display: grid;
grid-template-columns: repeat(2, 1fr); /* 2개의 열로 설정 */
gap: 10px;
`;
// Box 컴포넌트에서 custom prop를 사용하여 확대되는 방향을 설정
const Box = styled(motion.div).attrs((props) => ({
  custom: props.custom,
}))`
  background-color: rgba(255, 255, 255, 0.8); 
  border-radius: 40px;
  width: 400px; 
  height: 200px; 
  box-shadow: 0px,2px,3px rgba(0,0,0,.1),0px,10px,20px rgba(0,0,0,.06);
  position:relative;
`;
const Overlay = styled(motion.div)`
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
`;

const ToggleButton = styled.button`
  margin-top: 20px; /* 상자들과 토글 버튼 사이에 간격 추가 */
`;


// 각 박스가 확대될 때 적용할 variants를 정의합니다.
const initialVariants = {
  hidden: { scale: 1 },
  hover: { scale: 1.1, transformOrigin: 'bottom right'  },
};
const Variants = {
  hidden: { scale: 1 },
  hover: { scale: 1.1, transformOrigin: 'top left'  },

};

const centerVariants = {
  hidden: { opacity: 0, y: -100 },
  visible: { opacity: 1, y: 0 },
  exit: { opacity: 0, y: -100 },
};


const Circle = styled(motion.div)`
  background-color: #FFFFFF;
  height: 100px;
  width: 100px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.06);
  position:absolute;
  top :20%;
  left :40%;
  transform :translate(-0%, -0%);
`;


function App() {
  const [id, setId] = useState<string | null>(null);
  const [isSwitchOn, setIsSwitchOn] = useState(false);
  const [isOnSecondBox, setIsOnSecondBox] = useState(false);

  const toggleSwitch = () => {
    setIsSwitchOn((prev) => !prev);
    setIsOnSecondBox((prev) => !prev);
    setId(null);
  };

  const handleOverlayClick = () => {
    setId(null);
  };

  const handleBoxClick = (n: string ) => {
    setId(n);
  };

  const handleOverlayExitComplete = () => {
    setId(null);
  };

  return (
    <Wrapper>
    <Grid>
      {["1", "2",'3','4'].map((n) => (
        <Box
          key={n}
          layoutId={n}
          variants={n === "1" ? initialVariants : n === '4' ? Variants : undefined}
          whileHover="hover"
          onClick={() => handleBoxClick(n)} // Box 클릭 시 해당 Box의 layoutId를 전달
        >
          {((n === "2" && !isOnSecondBox) || (n === '3' && isOnSecondBox)) && id !== n ? (
            <AnimatePresence>
              <Circle layoutId="circle" style={{ borderRadius: 50 }} />
            </AnimatePresence>
          ) : null}
        </Box>
        ))}
        {/* {["3", "4"].map((n) => (
          <Box
            key={n}
            layoutId={n}
            variants={n === "4" ? Variants : undefined}
            whileHover="hover"
            onClick={() => handleBoxClick(n)} // Box 클릭 시 해당 Box의 layoutId를 전달
          >
            {n === "3" && isOnSecondBox ? (
              <AnimatePresence>
                <Circle layoutId="circle" style={{ borderRadius: 50 }} />
              </AnimatePresence>
            ) : null}
          </Box>
        ))} */}
      </Grid>
      <AnimatePresence onExitComplete={handleOverlayExitComplete}>
        {id && !isSwitchOn && (
          <Overlay
            variants={centerVariants}
            initial="hidden"
            animate="visible"
            exit="exit"
            onClick={handleOverlayClick} // Overlay 클릭 시 layoutId를 null로 변경
          >
            <Box
              layoutId={id}
              style={{ width: 400, height: 200 }}
              variants={initialVariants}
              whileHover="hover"
              custom={parseInt(id) - 1}
            />
          </Overlay>
        )}
      </AnimatePresence>

      <ToggleButton onClick={toggleSwitch}>Switch</ToggleButton>
    </Wrapper>
  );
}

export default App;